* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 10px;
  font-weight: 100;
}

html body {
  height: 1200vh;
}

#header-sec {
  z-index: 1;
  background-color: red;
  width: 100%;
  position: fixed;
  top: 0%;
}

#header-sec .page-scroll-bar {
  width: 100%;
  height: 10px;
}

#header-sec .page-scroll-bar progress {
  appearance: none;
  background-color: #000;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  display: block;
}

#header-sec .page-scroll-bar progress::-webkit-progress-bar {
  background-color: #000;
}

#header-sec .page-scroll-bar progress::-webkit-progress-value {
  background-color: #1f9921;
}

#header-sec .page-scroll-bar progress::-moz-progress-bar {
  background-color: #1f9921;
}

#header-sec .nav-bar {
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
  height: fit-content;
  padding-block: 10px;
  padding-inline: 20px;
  display: flex;
}

#header-sec .nav-bar .greetings, #header-sec .nav-bar .topic-btn .aTag {
  color: #fff;
  font-size: 2rem;
  font-weight: 700;
}

#header-sec .nav-bar .greetings {
  margin-right: auto;
}

#header-sec .nav-bar .topic-btn .aTag {
  cursor: pointer;
  background-color: #000;
  padding: 5px 10px;
  display: block;
}

#header-sec .nav-bar .topic-btn .aTag:hover {
  transform: translateX(2px)translateY(-2px);
  box-shadow: -2px 2px 10px 3px #ffffffa7;
}

#TecSkills-sec-cards {
  grid-template-columns: repeat(auto-fit, minmax(30px, 80px));
  justify-content: center;
  width: 100%;
  margin: auto;
  display: grid;
}

#TecSkills-sec-cards .tech-card {
  cursor: pointer;
  aspect-ratio: 1;
  background-color: #fff;
  overflow: hidden;
}

#TecSkills-sec-cards .tech-card .tech-img {
  -webkit-user-select: none;
  user-select: none;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

#TecSkills-sec-cards .tech-card .tech-img img {
  object-fit: contain;
  object-position: center;
  width: 80%;
}

#TecSkills-sec-cards .tech-card .tech-name {
  -webkit-user-select: none;
  user-select: none;
  word-break: break-all;
  color: #fff;
  background-color: #000000e9;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 900;
  transition: transform .2s;
  display: flex;
  transform: translateY(-100%);
}

#aboutMe-sec {
  width: 100%;
  height: fit-content;
  position: relative;
}

#aboutMe-sec .info {
  color: #ffffffba;
  height: fit-content;
}

#aboutMe-sec .info > .myName {
  font-size: 50px;
  font-weight: 800;
}

#aboutMe-sec .info > .mytitle {
  font-size: 20px;
  font-weight: 300;
}

#aboutMe-sec .backGround {
  z-index: -1;
  background-color: #6200ff;
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  transform: translateY(-100%);
}

#aboutMe-sec .backGround > .circelAnim {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

#aboutMe-sec .backGround > .circelAnim.anim1 {
  filter: blur(6px);
  background-color: #fff;
  animation: 7s linear infinite backGroundcircel2;
}

#aboutMe-sec .backGround > .circelAnim.anim2 {
  background-color: #fff;
  animation: 10s linear infinite backGroundcircel1;
  display: none;
}

#aboutMe-sec .backGround > .circelAnim.anim3 {
  background-color: #fff;
  animation: 15s linear infinite backGroundcircel1;
  display: none;
}

@keyframes backGroundcircel2 {
  0% {
    transform: translateX(-100%)translateY(-100%);
  }

  100% {
    transform: translateX(calc(100vw + 100%))translateY(1%);
  }
}
