
#aboutMe-sec{   
    position: relative;
    width: 100%;
    height: fit-content;
    // height: 100px;
    .info{
        color: rgba(255, 255, 255, 0.731);
        height: fit-content;
        >.myName{
            font-size: 50px;
            font-weight: 800;
        }

        >.mytitle{
            font-size: 20px;
            font-weight: 300;
        }
        // background-color: brown;
    }
    .backGround{
        position: absolute;
        transform: translateY(-100%);
        z-index: -1;
        height: 100%;
        width: 100%;
        background-color: rgb(98, 0, 255);
        // backdrop-filter: blur(200px);
        overflow: hidden;
        >.circelAnim{
            width: 100px;
            height: 100px;
            border-radius: 50%;

            // top: 0;
            // right: 10%;
      
            &.anim1{
                background-color: white;
                animation: backGroundcircel2   7s infinite;
                animation-timing-function: linear;
                filter: blur(6px);
            }
            &.anim2{
                display: none;
                background-color: white;
                animation: backGroundcircel1   10s infinite;
                animation-timing-function: linear;
            }
            &.anim3{
                display: none;
                background-color: white;
                animation: backGroundcircel1   15s infinite;
                animation-timing-function: linear;
            }
        }
    }

  
}

@keyframes backGroundcircel2 {

        0% {
          transform: translateX(-100%) translateY(-100%);
          
        }
        100%{
            transform: translateX(calc( 100vw + 100%) ) translateY(calc( 1% ) );
        }
    
}

